.row {
	display: flex;
	flex-wrap: wrap;
	margin-left: -25px;
	margin-right: -25px;
}

.row [class*='col-'] {
	padding-left: 25px;
	padding-right: 25px;
}

.row.no_gutter [class*='col-'] {
	padding-left: 0;
	padding-right: 0;
}

.col-6 {
	width: 50%;
	@include tablet-sm-768 {
		width: 100%;
	}
}

.rps {
	height: 100vh;
	margin: 0 auto;
	max-width: 1200px;
	padding-left: 15px;
	padding-right: 15px;
	display: flex;
	flex-direction: column;
	justify-content: center;
	@media (max-width: 991px) {
		height: auto;
		padding: 50px 15px;
	}


	h1 {
		text-align: center;
		margin-bottom: 20px;
		font-size: 50px;
		color: #32313B;
		@media (max-width: 991px) {
			font-size: 35px;
		}
	}

	h2 {
		text-align: center;
		font-size: 30px;
		color: #70A4EE;

		@media (max-width: 991px) {
			font-size: 25px;
		}

		&.title_top {
			margin-bottom: 40px;
			@media (max-width: 768px) {
				margin-bottom: 20px;
			}
		}

		&.title_bottom {
			margin-top: 40px;
			@media (max-width: 768px) {
				margin-top: 0;
			}
		}
	}

	.item {
		height: 300px;
		background: #FFFFFF;
		border: 2px solid #F0EBE6;
		border-radius: 30px;
		padding: 30px;
		display: flex;
		align-items: center;
		justify-content: center;
		flex-direction: column;
		@media (max-width: 768px) {
			margin-bottom: 30px;
			height: 200px;
		}
		@media (max-width: 768px) {
			margin-bottom: 20px;
		}

		@media (max-height: 600px) {
			height: 270px;
		}

		.info {
			margin-bottom: 40px;

			h3 {
				font-size: 30px;
				color: #32313B;
				@media (max-width: 768px) {
					font-size: 23px;
				}

				span {
					color: #6DA7FB;
				}
			}
		}

		.show {
			font-size: 150px;
			color: #f1b25d;
			height: 150px;

			@media (max-width: 768px) {
				font-size: 80px;
				height: auto;
			}
		}
	}

	.selection {
		display: flex;
		align-items: center;
		justify-content: center;
		margin-top: 30px;
		@media (max-width: 768px) {
			margin-top: 20px;
		}

		button {
			width: 48px;
			height: 48px;
			display: flex;
			align-items: center;
			justify-content: center;
			background: #4BBD8C;
			border-radius: 50%;
			margin-right: 20px;
			color: #fff;
			font-size: 25px;
			transition: .3s;

			&:hover {
				background: #F1B25D;
			}

			&:last-child {
				margin-right: 0;
			}
		}
	}
}

.btn {
	height: 56px;
	padding: 0 30px;
	background: #4BBD8C;
	border-radius: 20px;
	font-weight: 500;
	font-size: 18px;
	color: #FFFFFF;
	display: inline-flex;
	align-items: center;
	justify-content: center;
	margin: 30px auto 0 auto;
	transition: .3s;

	&:hover {
		background: #f1b25d;
	}
}
