* {
  margin: 0;
  padding: 0;
  outline: none;
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  line-height: 1;
  min-width: 320px;
  position: relative;
}

article,
aside,
details,
figure,
figcaption,
footer,
header,
hgroup,
main,
nav,
section,
summary {
  display: block;
}

audio,
canvas,
progress,
video {
  display: inline-block;
  vertical-align: baseline;
}

fieldset {
  border: 0;
  padding: 0;
  margin: 0;
  min-width: 0;
}

.hidden,
[hidden],
template {
  display: none;
}

a {
  text-decoration: none;
  background: transparent;
}

a:active, a:hover {
  outline: 0;
}

a img {
  border: 0;
}

b,
strong {
  font-weight: bold;
}

small {
  font-size: 75%;
}

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sup {
  top: -.5em;
}

sub {
  bottom: -.25em;
}

svg:not(:root) {
  overflow: hidden;
}

button,
input,
optgroup,
select,
textarea {
  margin: 0;
  border: 0;
  font: inherit;
  color: inherit;
  appearance: none;
  border-radius: 0;
  text-transform: none;
  box-sizing: border-box;
  outline: none !important;
  background-clip: padding-box;
  background-color: transparent;
}

button,
html input[type='button'] {
  cursor: pointer;
  appearance: button;
}

button[disabled],
html input[disabled] {
  cursor: default;
}

button::-moz-focus-inner,
input::-moz-focus-inner {
  border: 0;
  padding: 0;
}

input {
  line-height: normal;
}

input[type='reset'], input[type='submit'] {
  cursor: pointer;
  appearance: button;
}

input[type='checkbox'], input[type='radio'] {
  box-sizing: border-box;
  padding: 0;
}

input[type='number']::-webkit-inner-spin-button, input[type='number']::-webkit-outer-spin-button {
  height: auto;
}

input[type='search'] {
  appearance: textfield;
  box-sizing: border-box;
}

input[type='search']::-webkit-search-cancel-button, input[type='search']::-webkit-search-decoration {
  appearance: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
}

td,
th {
  padding: 0;
}

h1,
h2,
h3,
h4,
h5 {
  margin: 0;
}

img {
  max-width: 100%;
  height: auto;
  pointer-events: none;
  user-select: none;
  border: 0;
}

.clearfix {
  clear: both;
}

body {
  color: #000;
  font-size: 16px;
  font-family: 'Roboto', sans-serif;
  background: #FCF9F5;
}

.row {
  display: flex;
  flex-wrap: wrap;
  margin-left: -25px;
  margin-right: -25px;
}

.row [class*='col-'] {
  padding-left: 25px;
  padding-right: 25px;
}

.row.no_gutter [class*='col-'] {
  padding-left: 0;
  padding-right: 0;
}

.col-6 {
  width: 50%;
}

@media (max-width: 768px) {
  .col-6 {
    width: 100%;
  }
}

.rps {
  height: 100vh;
  margin: 0 auto;
  max-width: 1200px;
  padding-left: 15px;
  padding-right: 15px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

@media (max-width: 991px) {
  .rps {
    height: auto;
    padding: 50px 15px;
  }
}

.rps h1 {
  text-align: center;
  margin-bottom: 20px;
  font-size: 50px;
  color: #32313B;
}

@media (max-width: 991px) {
  .rps h1 {
    font-size: 35px;
  }
}

.rps h2 {
  text-align: center;
  font-size: 30px;
  color: #70A4EE;
}

@media (max-width: 991px) {
  .rps h2 {
    font-size: 25px;
  }
}

.rps h2.title_top {
  margin-bottom: 40px;
}

@media (max-width: 768px) {
  .rps h2.title_top {
    margin-bottom: 20px;
  }
}

.rps h2.title_bottom {
  margin-top: 40px;
}

@media (max-width: 768px) {
  .rps h2.title_bottom {
    margin-top: 0;
  }
}

.rps .item {
  height: 300px;
  background: #FFFFFF;
  border: 2px solid #F0EBE6;
  border-radius: 30px;
  padding: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

@media (max-width: 768px) {
  .rps .item {
    margin-bottom: 30px;
    height: 200px;
  }
}

@media (max-width: 768px) {
  .rps .item {
    margin-bottom: 20px;
  }
}

@media (max-height: 600px) {
  .rps .item {
    height: 270px;
  }
}

.rps .item .info {
  margin-bottom: 40px;
}

.rps .item .info h3 {
  font-size: 30px;
  color: #32313B;
}

@media (max-width: 768px) {
  .rps .item .info h3 {
    font-size: 23px;
  }
}

.rps .item .info h3 span {
  color: #6DA7FB;
}

.rps .item .show {
  font-size: 150px;
  color: #f1b25d;
  height: 150px;
}

@media (max-width: 768px) {
  .rps .item .show {
    font-size: 80px;
    height: auto;
  }
}

.rps .selection {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 30px;
}

@media (max-width: 768px) {
  .rps .selection {
    margin-top: 20px;
  }
}

.rps .selection button {
  width: 48px;
  height: 48px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #4BBD8C;
  border-radius: 50%;
  margin-right: 20px;
  color: #fff;
  font-size: 25px;
  transition: .3s;
}

.rps .selection button:hover {
  background: #F1B25D;
}

.rps .selection button:last-child {
  margin-right: 0;
}

.btn {
  height: 56px;
  padding: 0 30px;
  background: #4BBD8C;
  border-radius: 20px;
  font-weight: 500;
  font-size: 18px;
  color: #FFFFFF;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  margin: 30px auto 0 auto;
  transition: .3s;
}

.btn:hover {
  background: #f1b25d;
}

main {
  position: relative;
  overflow: hidden;
}
